export const idBodyTemplate = (rowData) => {
    return <h2 className='text-[#6f6b7d]'>{rowData.id}</h2>
};

export const nameBodyTemplate = (rowData) => {

    return <p className='mb-1 capitalize text-[13px] font-medium'>{rowData.name}</p>
};

export const emailBodyTemplate = (rowData) => {

    return <p className='mb-1 capitalize text-[13px] font-medium'>{rowData.email}</p>

};

export const officialNameBodyTemplate = (rowData) => {
    return <p className='mb-1 capitalize text-[13px] font-medium'>{rowData.officialName}</p>
};

export const commercialNameBodyTemplate = (rowData) => {
    return <p className='mb-1 capitalize text-[13px] font-medium'>{rowData.commercialName}</p>
};

export const locationBodyTemplate = (rowData) => {
    const items = ['Country', 'Provice', 'City'];
    return rowData.location.map((item, index) => {
        return <div className='flex items-center'>
            <p className='capitalize text-[13px] opacity-70 font-medium me-2'>{items[index]}: </p>
            <p key={index} className='mb-1 capitalize text-[13px] font-medium'>{item}</p>

        </div>
    })
};

export const lastDateBodyTemplate = (rowData) => {

    return <p className='mb-1 capitalize text-[13px] font-medium'>{rowData.lastDate}</p>
};

export const actionsBodyTemplate = (rowData) => {

    return <TableActions rowData={rowData}></TableActions>

};

export const getCustomers = (data) => {
    return [...(data || [])].map((d) => {
        d.date = new Date(d.date);

        return d;
    });
};